          cover: require("./assets/images/19.jpg"),
        },
      ],
      player: new Audio(),
      transit: null,
      show: true,
      volume: 100,
      opened: false,
    };
  },
  methods: {
    documentClick(e) {
      let el = this.$refs.volumeSlider;
      let es = this.$refs.volumeBtn;
      let target = e.target;
      if (el !== target && !el.contains(target) && es !== target) {
        this.opened = false;
      }
    },
    setVolume() {
      this.player.volume = this.volume / 100;
    },
    play(song) {
      if (typeof song.src != "undefined") {
        this.current = song;
        this.player.src = this.current.src;
      }
      this.player.play();
      this.isPlaying = true;
      document.title = this.current.title + " - " + this.current.artist;
    },
    pause() {
      this.player.pause();
      this.isPlaying = false;
    },
    next() {
      this.index++;
      if (this.index > this.songs.length - 1) {
        this.index = 0;
      }
      this.current = this.songs[this.index];
      this.play(this.current);
      this.transit = "scale-out";
    },
    prev() {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }
      this.current = this.songs[this.index];
      this.play(this.current);
      this.transit = "scale-in";
    },
    replay() {
      this.player.pause();
      this.player.currentTime = 0;
      this.current = this.songs[this.index];
      this.play(this.current);
    },
    shuffle() {
      let min = 0;
      let max = this.songs.length - 1;
      let random = Math.floor(Math.random() * (+max - +min) + +min);
      this.index = random;
      this.current = this.songs[this.index];
      this.play(this.current);
    },
    download() {
      this.pause();
      window.open(this.player.src, "download");
    },

    //Title animation, doesn't work yet
